            backdrop-filter: blur(5px);
    overflow: hidden;
    transition: 0.5s linear;
  }
  .card-glass:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    width: auto;
    z-index: 99;
  }
  .hidden{
